.title{
    text-align: center;
    font-size: 20px;
    padding: 30px;
}

.userBox{
    background-color: #1b1b1b;
    padding-top: 25px;
    border-radius: 3px;
    margin-bottom: 40px;
}

.userBox:hover{
    text-decoration: none;
}

.userBox h4, .userBox p{
    color: #fff;
}

.userBox h4:hover, .userBox p:hover{
    color: #fff;
    text-decoration: none;
}

.userBox img {
    width: 70px;
    height: 70px;
    border-radius: 5px;
    border: 2px solid #fff;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.2);
    margin-bottom: 15px;
}

.location{
    font-size: 12px;
}

.status{
    margin: 20px 0;
}

.userBox button{
    padding: 20px;
    width:100%;
    background-color: #fa2276;
    border: none;
    outline: none;
    color: #fff;
}

.selectedPage{
    font-weight: bold;
}

.pages{
    margin-bottom: 30px;
}

