.navbar{
    background-color: #1a1a1a;
    text-align: left;
    position: fixed;
    left: 0;
    padding: 15px;
    display: flex;
    width: 15%;
    flex-direction: column;
    height: 100%;
  }

  .menuWrapper{
    border-bottom: 1px solid #ddd;
  }
  
  nav div{
    padding: 10px 0;
  }
  
  
  nav i{
    font-size: 20px;
    margin-right: 10px;
  }
  
  nav a {
    color: #fff;
    transition: .3s;
  }

  nav a.active{
    color: #949494;
  }
  
  nav a:hover{
    color: #fff;
  }

  .friendsWrapper{
    margin-top: 30px;
  }

  .friends{
    display: flex;
    justify-content: space-between;
  }


