header{
    background-color: #1a1a1a;
    color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 5px;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
 .logo{
   width: 80px;
 }

 .brand{
   width: 230px;
   margin-left: 30px;
 }
  
  header h2{
    font-size: 30px;
    margin-left: 10px;
  }

  .item{
    display: flex;
    align-self: center;
    display: flex;
    align-items: center;
  }
  
.item a{
    padding:0 20px;
    color: #fff;
  }

.item a i{
  font-size: 20px;
}  

  
  .profileName{
    border-left: 1px solid #fff;
    padding: 0 15px;
    margin-left: 50px;
    display: flex;
    align-items: center;
  }
  
  .profileName a{
    color: #fff;
    padding-left: 5px;
  }
  
  .profileName a:hover{
    text-decoration: none;
    color: #fff;
  }